  /* 2 */ }

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

html,
button,
input,
select,
textarea {
  color: #222; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

img {
  vertical-align: middle; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

textarea {
  resize: vertical; }

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0; }

:root {
  --line-width: 10px; }

* {
  box-sizing: border-box; }

html {
  font-size: 100px;
  font-size: calc(80px + (100 - 80) * ((100vw - 1440px) / (1920 - 1440))); }
  @media (max-width: 1440px) {
    html {
      font-size: 80px; } }
  @media (min-width: 1920px) {
    html {
      font-size: 100px; } }
  @media only screen and (max-width: 1440px) {
    html {
      font-size: 80px;
      font-size: calc(50px + (80 - 50) * ((100vw - 1024px) / (1440 - 1024))); } }
  @media only screen and (max-width: 1440px) and (max-width: 1024px) {
    html {
      font-size: 50px; } }
  @media only screen and (max-width: 1440px) and (min-width: 1440px) {
    html {
      font-size: 80px; } }

body {
  color: #fff;
  font-size: 16px; }
  body.is-blocked {
    height: 100vh;
    height: calc(1 * 100);
    height: calc(var(--vh, 1) * 100);
    overflow: hidden;
    width: calc(1 * 100);
    width: calc(var(--vw, 1) * 100);
    width: 100vw; }

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.3;
  font-family: 'Montserrat-Bold', Helvetica, sans-serif; }

section {
  min-height: 100vh; }

.app {
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(95%, #140e1c), color-stop(13%, #08060a), color-stop(-4%, #000000), color-stop(-31%, #120e17));
  background-image: linear-gradient(to bottom, #140e1c 95%, #08060a 13%, #000000 -4%, #120e17 -31%);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Montserrat-Light', Helvetica, sans-serif; }
  .app__body {
    flex-grow: 1;
    min-height: 100%;
    overflow: hidden; }

