@import 'style/helpers';

@mixin gradient {
  background-image: linear-gradient(to bottom, #ffffff, rgba(255, 255, 255, 0.45)), linear-gradient(64deg, #0a050d -7%, #120e17 104%);
  opacity: 0.05; }

.slider {
  position: relative;

  &__item {
    color: #fff;
    height: 100%;
    padding: 1rem;
    position: relative;
    margin: auto;
    max-width: calc(100% - 0.3rem);
    text-decoration: none;
    user-select: none;

    &:before {
      content: '';
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%;
      @include gradient; }

    img {
      display: block;
      margin: auto;
      max-height: 1.5rem;
      max-width: 3.8rem;
      width: 100%; } }

  &__content {
    display: flex;
    flex-direction: column;
    height: 100%; }

  &__text {
    flex-grow: 1;
    line-height: 1.67;
    letter-spacing: 0.95px;
    margin-bottom: 0.8rem;
    text-align: center;
    @include css-lock(24, 30, $desktop, $desktopHD);
    @include regular;

    @include lg {
      @include css-lock(16, 24, $tablet, $desktop); } }

  &__btn {
    cursor: pointer;
    height: 100%;
    position: absolute;
    top: 0;
    width: 2rem;

    &_prev {
      left: 0.15rem;
      transform: translateX(-100%);

      @include md {
        left: 35px; } }

    &_next {
      right: 0.45rem;
      transform: translateX(100%);

      @include md {
        right: 45px; } } }

  .slick-track {
    align-items: stretch;
    display: flex; }

  .slick-list {
    overflow: visible; }

  .slick-slide {
    align-items: stretch;
    display: flex;
    height: auto;

    div {
      width: 100%; } } }
