@import 'style/helpers';

.header {
  align-items: center;
  display: flex;
  left: 0;
  @include padding;
  padding-bottom: 40px;
  padding-top: 40px;
  position: fixed;
  top: 0;
  justify-content: space-between;
  width: 100%;
  z-index: 5;
  @include css-lock(15, 18, $desktop, $desktopHD);
  @include semi-bold;

  @include lg {
    @include css-lock(12, 15, $tablet, $desktop); }

  @include md {
    font-size: 18px;
    padding: 24px 16px; }

  &.is-fixed {
    &:before {
      transform: translateY(0); } }

  &:before {
    background-color: #202022;
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    transform: translateY(-100%);
    transition: transform ease-out 0.3s;
    width: 100%; }

  &__logo {
    position: relative;
    width: 134px;

    @include lg {
      width: 120px; } }

  &__nav {
    padding: 0 20px;

    @include md {
      display: none; } }

  &__btns {
    align-items: center;
    display: flex;
    justify-content: center; }

  &__burger {
    display: none;
    margin-left: 28px;
    z-index: 6;

    @include md {
      display: block; } } }
