@import 'variables';
@import 'mixins';
@import 'helpers';
@import 'reset';

 :root {
  --line-width: 10px; }

* {
  box-sizing: border-box; }

html {
  @include css-lock(80, 100, $desktop, $desktopHD);
  @include lg {
    @include css-lock(50, 80, $tablet, $desktop); } }

body {
  color: #fff;
  font-size: 16px;

  &.is-blocked {
    height: 100vh;
    height: calc(var(--vh, 1) * 100);
    overflow: hidden;
    width: calc(var(--vw, 1) * 100);
    width: 100vw; } }

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.3;
  @include bold; }

section {
  min-height: 100vh; }

.app {
  background-image: linear-gradient(to bottom, #140e1c 95%, #08060a 13%, #000000 -4%, #120e17 -31%);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  @include light;

  &__body {
    flex-grow: 1;
    min-height: 100%;
    overflow: hidden; } }
