@import 'style/helpers';

.benefits {
  background-image: url('/img/bg.jpg');
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  height: calc(var(--vh, 1) * 100);

  &__row {
    display: flex;
    margin-bottom: 20vmin;
    width: 100%;

    @include md {
      display: contents; } }

  &__item {
    align-items: center;
    display: flex;
    height: 100%;
    flex-wrap: wrap;
    flex-grow: 1;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    @include padding;

    @include md {
      flex-direction: column;
      padding-top: 15vmin;
      padding-bottom: 15vmin; } }

  &__headline,
  &__text {
    width: 50%;

    @include md {
      width: 100%; } }

  &__headline {
    font-size: 8.3vmin;
    line-height: 1.1;
    letter-spacing: 0.26vmin;
    position: relative;
    background-clip: text;
    text-fill: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    @include medium;

    @include lg {
      font-size: 6.6vmin; }

    @include md {
      font-size: 6.6vmin;
      flex-grow: 1;
      margin-bottom: 7.5vmin;
      order: 1; }

    @include sm {
      font-size: 5vmin; }

    &_violet {
      background-image: linear-gradient(69deg, #f419a8 6%, #7f6fdb 40%, #00f1f9 73%); }

    &_pink {
      background-image: linear-gradient(70deg, #ff2dd6 10%, #f12d2d 49%, #e1f701 85%); }

    &_green {
      background-image: linear-gradient(to right, #ff854f 6%, #ffeb4f 43%, #00e300 80%); }

    &_blue {
      background-image: linear-gradient(78deg, #7f6fdb 7%, #00f1f9 57%, #00f95f 99%); }

    &_dark-blue {
      background-image: linear-gradient(76deg, #00f1f9 7%, #7f6fdb 54%, #f419a8 102%); }

    &_orange {
      background-image: linear-gradient(73deg, #00e300 -2%, #ffeb4f 44%, #ff7649 92%); }

    span {
      background-image: inherit;
      display: inline-block;
      max-width: 770px;
      background-clip: text;
      text-fill: transparent;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      &:nth-of-type(1),
      &:nth-of-type(2) {
        left: 0;
        position: absolute;
        top: 0;
        transform: translate(0, 0);
        transition: transform ease-out 0.3s, visibility ease-out 0.15s;
        visibility: hidden;
        width: 100%;
        @include hairline; }

      &:nth-of-type(1) {
        opacity: 0.2; }

      &:nth-of-type(2) {
        opacity: 0.5; } }

    .is-animated & {
      span {
        visibility: visible;

        &:nth-of-type(1) {
          transform: translate(-0.8rem, -0.7rem);
          @include md {
            transform: translate(-16px, -18px); } }

        &:nth-of-type(2) {
          transform: translate(-0.4rem, -0.5rem);
          @include md {
            transform: translate(-10px, -12px); } } } } }

  &__text {
    line-height: 1.5;
    margin-left: 15vmin;
    max-width: 730px;
    @include css-lock(24, 30, $desktop, $desktopHD);
    @include regular;

    @include lg {
      @include css-lock(18, 24, $tablet, $desktop); }

    @include md {
      font-size: 3vmin;
      order: 3;
      margin-left: 0;
      max-width: 100%; }

    @include sm {
      font-size: 2.4vmin; } }

  &__icons {
    width: 100%;

    @include md {
      margin-bottom: 38px;
      order: 2; } }

  &__icon {
    display: inline-block;
    height: 0.8rem;

    img {
      height: 100%;
      display: block; }

    @include sm {
      height: 20px; }

    &:not(:last-of-type) {
      margin-right: 0.6rem; } }

  video {
    height: 100%;
    left: 0;
    object-fit: cover;
    position: absolute;
    top: 0;
    width: 100%;

    @include md {
      display: none; } } }
